import {memo, ReactNode} from 'react';
import {classNames, Mods} from "shared/lib/classNames/classNames";
import cls from "./CalendarContainer.module.scss"

interface CalendarContainerProps {
    className?: string
    children?: ReactNode
}


export const CalendarContainer = memo((props: CalendarContainerProps) => {
    const {
        className,
        children,
        ...otherProps
    } = props

    const mods: Mods = {

    };

    return (
        <div
            className={classNames(cls.CalendarContainer, mods, [className])}
            {...otherProps}
        >
            {children}
        </div>
    );
});